import React, { useContext } from "react"
import AppContext from '../contextAPI/AppContext'
import { navigate } from "gatsby"
import SEO from "../components/seo"

const IndexPage = () => {
  const receivedAppContext = useContext(AppContext)
return (
  <div>
    <SEO title="Home" />
    {/* <img src={SVLogo} classNames={styles.logo}/> */}
    <p onClick={() => {
      receivedAppContext.updateLanguage("GRK")
      receivedAppContext.updateVersion("GNA")
      navigate("/GRK/GNA/I042")}}
    >GREEK</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("HBR")
      receivedAppContext.updateVersion("WLC")
      navigate("/HBR/WLC/I032")}}
    >HEBREW</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("ENG")
      receivedAppContext.updateVersion("NLT")
      navigate("/ENG/NLT/I042")}}
    >ENGLISH: NLT</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("ENG")
      receivedAppContext.updateVersion("WEB")
      navigate("/ENG/WEB/I042")}}
    >ENGLISH: WEB</p>
  </div>
)
}

export default IndexPage
